.nav__list {
  display: flex;
}

.nav__item {
  position: relative;
  padding-right: 26px;
  padding-left: 10px;
}

.nav__item:last-child:after {
  display: none;
}

.nav__item:after {
  content: "|";
  color: #fff;
  position: absolute;
  right: 5px;
  top: 0;
  font-size: 19px;
}

.nav__item:hover .nav__link, .nav__item.is-active .nav__link {
  color: #90c53c;
}

.nav__item.is-active .nav__link {
  font-family: AzoSans-Regular;
  font-weight: normal;
}

.nav__link {
  color: #ffffff;
  text-decoration: none;
  font-family: AzoSans-Light;
  font-weight: normal;
  font-size: 17px;
  line-height: 1.533;
}

.nav__item-menu {
  position: relative;
}

.nav__item-menu:hover:before {
  border-top-color: #90c53c;
}

.nav__item-menu:hover .nav__link {
  color: #90c53c;
}

.nav__item-menu a {
  padding-right: 20px;
}

.nav__item-menu:before {
  content: '';
  width: 0px;
  border: 5px solid transparent;
  border-top: 7px solid #fff;
  position: absolute;
  right: 25px;
  top: 8px;
}

.nav__item-drop {
  display: none;
  position: absolute;
  border: 1px solid #fff;
  padding: 10px;
  right: 0;
  flex-flow: column;
  border-radius: 12px;
  font-family: AzoSans-Light;
  font-weight: normal;
  width: 210px;
  background-color: #181818;
}

.nav__item-drop.is-active {
  display: flex;
}

.nav__item-drop li {
  margin-bottom: 10px;
}

.nav__item-drop li:last-child {
  margin-bottom: 0;
}

.nav__item-drop li:hover a {
  color: #90c53c;
}

.nav__item-drop li a {
  color: #fff;
  text-decoration: none;
}

.small-menu {
  position: absolute;
  top: 0;
  z-index: 10;
  background-color: #181818;
  width: 100vw;
  height: 100vh;
  margin-left: -15px;
  display: none;
  padding: 55px 0 15px;
}

.small-menu.is-active {
  display: flex;
  justify-content: center;
}

.small-menu__close {
  width: 25px;
  height: 25px;
  position: absolute;
  right: 40px;
  background-color: transparent;
  top: 20px;
}

.small-menu__close:hover:before, .small-menu__close:hover:after {
  background-color: #90c53c;
}

.small-menu__close:before {
  content: '';
  width: 20px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  transform: rotate(45deg);
}

.small-menu__close:after {
  content: '';
  width: 20px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  transform: rotate(135deg);
}

.small-menu__item {
  text-align: center;
  margin-bottom: 10px;
}

.small-menu__item:hover .small-menu__link, .small-menu__item.is-active .small-menu__link {
  color: #90c53c;
  font-family: AzoSans-Regular;
  font-weight: normal;
}

.small-menu__link {
  color: #fff;
  text-decoration: none;
  font-family: AzoSans-Light;
  font-weight: normal;
  font-size: 24px;
}

.small-menu__item-menu {
  position: relative;
}

.small-menu__item-menu:hover:after, .small-menu__item-menu.is-active:after {
  border-top-color: #90c53c;
}

.small-menu__item-menu:after {
  content: '';
  width: 0px;
  border: 5px solid transparent;
  border-top: 7px solid #fff;
  position: absolute;
  right: -10px;
  top: 8px;
}

.small-menu__item-drop {
  margin-top: 10px;
  display: none;
}

.small-menu__item-drop.is-active {
  display: block;
}

.small-menu__item-drop li {
  margin-bottom: 10px;
}

.small-menu__item-drop li:hover a {
  color: #90c53c;
  font-family: AzoSans-Regular;
  font-weight: normal;
}

.small-menu__item-drop li a {
  color: #fff;
  text-decoration: none;
  font-family: AzoSans-Light;
  font-weight: normal;
}

