@include font(AzoSans-Bold, AzoSans-Bold);
@include font(AzoSans-Light, AzoSans-Light);
@include font(AzoSans-Regular, AzoSans-Regular);
@include font(AzoSans-Thin, AzoSans-Thin);

@mixin fAB {
	font-family: AzoSans-Bold;
	font-weight: normal; }

@mixin fAL {
	font-family: AzoSans-Light;
	font-weight: normal; }

@mixin fAR {
	font-family: AzoSans-Regular;
	font-weight: normal; }

@mixin fATh {
	font-family: AzoSans-Thin;
	font-weight: normal; }


.h2, .h3, .h2-subheading, .h3-subheading {
	text-align: center; }

.h1 {
	color: #ffffff;
	font-size: 63px;
	font-weight: normal;
	@include fAR;
	line-height: 1.0952;
	@include r(1400) {
		font-size: 50px; }
	@include r(900) {
		font-size: 36px; }
	@include r(460) {
		font-size: 24px; } }

.h1-subheading {
	color: #ffffff;
	font-size: 63px;
	line-height: 1.0952;
	@include fATh;
	font-weight: normal;
	@include r(1400) {
		font-size: 50px; }
	@include r(900) {
		font-size: 36px; }
	@include r(460) {
		font-size: 24px; } }
.h2 {
	color: #181818;
	@include fAR;
	font-size: 42px;
	font-weight: normal;
	line-height: 52.08px;
	@include r(440) {
		font-size: 28px;
		line-height: 1; } }

.h2-subheading {
	color: #181818;
	@include fAL;
	font-size: 42px;
	font-weight: normal;
	line-height: 1.24;
	@include r(440) {
		font-size: 28px;
		line-height: 1; } }

.h3 {
	color: #181818;
	@include fAB;
	font-size: 29px;
	font-weight: normal;
	line-height: 1;
	text-align: center;
	margin-bottom: 10px; }

.h3-subheading {
	color: #181818;
	font-size: 24px;
	@include fAR;
	font-weight: normal;
	line-height: 1.2;
	text-align: center;
	letter-spacing: -0.5px; }

.h4 {
	font-size: 23px;
	@include fAR;
	line-height: 1.3478;
	color: #ffffff; }
